    verifyUserData() {
        
      let lesValiditeDeChamps = true;
      for (let input of document.querySelectorAll(".leFormulaire base-input")) {
        if (input.getAttribute("type") != "submit") {
          if (!input.reportValidity()) {
            lesValiditeDeChamps = false;
        
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "Vous avez mal complt ce champ";
          } else {
             
            document.getElementById(
              input.getAttribute("id").concat("ErrorMsg")
            ).textContent = "";
          }
        }
      }
      if (lesValiditeDeChamps) {
        fetch("https://achieve-back.herokuapp.com/user/signup", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            pseudo: this.pseudo,
            email: this.email,
            socialNetwork:this.socialNetwork,
            password: this.password,
          }),
        }).then(() => {
          this.$router.push("login");
        })
        .catch(()=>{
          
            ;});
      }
    },
  },
    created() {
    localStorage.setItem("AccessLeTokenUserAchieve", JSON.stringify({boolAppearRoom:false}));
  },
};
