 pourra faire lobjet de modification par la Socit en cas dvolutions rglementaires, jurisprudentielles ou techniques.\nLa Socit respecte la loi  Informatique & Liberts n 78-17 du 6 janvier 1978 modifie, ainsi que la loi pour la confiance dans lconomie numrique n 2004-575 du 21 juin 2004, ainsi que le Rglement Gnral sur la Protection des Donnes, n 2016/679 du 27 avril 2016.\n\n"),_c('br'),_c('br'),_vm._v("ARTICLE 1  DFINITION\nLe Rglement Gnral sur la Protection des Donnes concerne le traitement et la circulation des donnes  caractre personnel, ces informations sur lesquelles les entreprises sappuient pour proposer des services et des produits.\nIl tablit des rgles relatives  la protection des personnes physiques  lgard du traitement des donnes  caractre personnel et des rgles relatives  la libre circulation de ces donnes.\nIl protge les liberts et droits fondamentaux des personnes physiques et en particulier leur droit  la protection des donnes  caractre personnel.\nLes principaux objectifs du RGPD sont daccrotre  la fois la protection des personnes concernes par un traitement de leurs donnes  caractre personnel et la responsabilisation des acteurs de ce traitement.\nLobjectif est galement dharmoniser la norme juridique europenne en matire de protection des donnes personnelles, afin quil ny ait quun seul et mme cadre sappliquant  lensemble des tats membres.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 2  NOTION DE DONNES PERSONNELLES\nUne donne personnelle est une information qui permet didentifier une personne physique, directement ou indirectement. Il peut sagir dun nom, dune photographie, dune adresse IP, dun numro de tlphone, dun identifiant de connexion informatique, dune adresse postale, dune empreinte, dun enregistrement vocal, dun numro de scurit sociale, dune adresse email, etc.\nCertaines donnes sont sensibles, car elles touchent  des informations qui peuvent donner lieu  de la discrimination ou des prjugs: une opinion politique, une sensibilit religieuse, un engagement syndical, une appartenance ethnique, une orientation sexuelle, une situation mdicale ou des ides philosophiques sont des donnes sensibles.\nElles ont un cadre particulier, qui interdit toute collecte pralable sans consentement crit, clair et explicite, et pour des cas prcis, valids par la Commission Nationale de lInformatique et des Liberts  CNIL et dont lintrt public est avr.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 3  DONNES COLLECTES AU SEIN DE LENTREPRISE\nLes informations peuvent tre collectes de faons diffrentes\nLe consentement\nLa Socit ne collecte aucune donne personnelle sans recueillir le consentement exprs et donner au pralable des informations concernant notamment le type de donnes collectes, leurs finalits, le responsable de leur traitement, et les diffrents droits que les personnes  lorigine des donnes sont  mme dexercer sur ces dernires.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 4  LOBLIGATION dINFORMATION ET LE RESPECT DU CONSENTEMENT\nLa Socit garantit les droits daccs, de rectification et dopposition  lutilisation de leurs donnes.\nElle garantit galement le droit  la limitation du traitement, le droit  loubli,  le droit  leffacement des donnes ou, le cas chant, le droit  la portabilit des donnes.\nLa protection des mineurs de moins de 16 ans est galement renforce. Le consentement du titulaire de lautorit parentale doit tre donn.\n chaque collecte de donnes, la personne concerne doit tre informe du fondement juridique sur lequel le traitement est effectu, de ses droits sur le traitement (limitation, portabilit et recours) et des modalits exactes du traitement de ses donnes.\nCes informations doivent tre visibles et accessibles sur le site internet o les donnes sont collectes, ou le cas chant, sur les supports qui permettent la collecte des donnes contrats signs, etc.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 5  FINALITS DES DONNES COLLECTES\nSeules les donnes ncessaires et pertinentes au regard des finalits poursuivies sont collectes, dans le respect du principe de proportionnalit et ce afin damliorer la qualit des produits ou services que la Socit propose.\nLa Socit ne collectera que les donnes adquates, pertinentes et strictement ncessaires  la finalit du traitement.\nLes donnes identifies comme tant obligatoires sont ncessaires afin de pouvoir bnficier des fonctionnalits correspondantes et plus spcifiquement des oprations sur les contenus proposs au sein de lentreprise.\nCette politique concerne la Socit et ses sites, les applications, les logiciels et services dits par la Socit et/ou utilisant son interface ou ses fonctionnalits.\nARTICLE 6  UTILISATION DES DONNES COLLECTES\nLes Donnes collectes par lentreprise sont traites pour les besoins dexcution des oprations sur les contenus du service.\nCette utilisation repose sur lun des fondements juridiques prvus par la loi soit:\nla protection des intrts lgitimes de lentreprise,\nlexcution dun contrat conclu ou dun engagement,\nle respect dune obligation lgale ou rglementaire,\nla prservation de lintrt public, comme la prvention ou la dtection dune fraude ou dun dlit financier.\nEn aucun cas, les donnes ne seront traites dune manire incompatible avec ces finalits, sauf  recueillir un accord pralable.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 7  SCURIT DES DONNES\nLes donnes personnelles recueillies par la Socit ne sont en aucun cas cdes, loues ou changes  des tiers,  lexception des partenaires et filiales de la Socit,  moins que cela nait t clairement prcis lors de la collecte des donnes concernes.\nToutefois les donnes pourront tre divulgues en application dune loi, dun rglement ou en vertu dune dcision dune autorit rglementaire ou judiciaire comptente ou encore, si cela savre ncessaire, aux fins de prserver ses droits et intrts.\nPar ailleurs la Socit pourra, le cas chant, transmettre des informations si elle acquiert une autre socit ou fait lobjet dun rachat, dune fusion, dune absorption, dun regroupement ou dune rorganisation de quelque nature que ce soit.\nTout utilisateur ouvrant un compte est invit  crer un identifiant ou pseudo et un mot de passe. Ce mot de passe doit imprativement rester secret et il doit limiter laccs  son ordinateur ou aux appareils mobiles et se dconnecter  la fin de lutilisation des services.\nLes donnes personnelles tant confidentielles, la socit limite leur accs aux seuls collaborateurs de la socit ou prestataires ayant besoin dans le cadre de lexcution du traitement.\nToutes les personnes ayant accs aux donnes personnelles sont lies par un devoir de confidentialit et sexposent  des mesures disciplinaires et/ou autres sanctions si elles ne respectent pas ces obligations.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 8  DURE DE CONSERVATION DES DONNES\nLes donnes sont stockes et conserves pour la dure ncessaire  la ralisation des finalits vises.\nLes donnes personnelles seront ainsi conserves pour la priode pendant laquelle les Salaris de la Socit utilisent les services support desdites donnes.\nLes donnes prcites sont supprimes au plus tard 5 ans  compter du dernier contact avec la personne ou les Salaris  lorigine desdites donnes.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 9  LES DROITS CONCERNS\nLa Socit entend respecter lintgralit des droits  lgard du traitement de donnes Personnelles vis--vis de Salaris :\nle droit dtre inform sur lutilisation des Donnes Personnelles ;\nle droit daccder aux informations personnelles recueillies auprs des Salaris de la Socit ;\nle droit de demander la correction des Donnes Personnelles inexactes, incompltes, quivoques ; primes pour les Salaris de la Socit ;\nla possibilit dexiger la transfrabilit (droit  la portabilit) des donnes  un autre fournisseur/utilisateur de service ;\nle droit de dfinir des directives relatives au sort des Donnes Personnelles aprs la mort  ;\nle droit de dposer le cas chant des plaintes justifies et dment motives auprs de lautorit nationale en charge de la protection des donnes Personnelles.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 10  SANCTION EN CAS DE NON-CONFORMIT\nEn cas de manquement aux obligations imposes par le RGPD, les entreprises concernes peuvent se voir infliger une amende pouvant atteindre 20 millions deuros ou 4% du chiffre daffaires mondial pour les entits les plus importantes.\nLa CNIL pourra mettre des rponses en cas de violation de la rglementation comme des mises en demeure ou des avertissements.\n \n"),_c('br'),_c('br'),_vm._v("ARTICLE 11  INFORMATION DU SALARI ET PUBLICIT\nLa prsente charte est accessible aux personnes concernes, utilisatrices de la solution Achieve, directement en cliquant sur un lien.\n"),_c('br'),_c('br'),_vm._v("ARTICLE 12  ENTRE EN VIGUEUR DE LA CHARTE\nLa prsente Charte est applicable ds la date de sa publication.\n \n \nFait  Toulouse, le 08/04/2022 ")])]),_c('template',{slot:"footer"},[_c('base-button',{staticClass:"ml-auto",attrs:{"type":"link","text-color":"white"},on:{"click":function($event){_vm.modals.modal2 = false}}},[_vm._v("\n                        Close\n                    ")])],1)],2)],1)]),_c('div',{staticClass:"text-center"},[_c('base-button',{staticClass:"my-4",attrs:{"type":"primary"},on:{"click":function($event){$event.preventDefault();return _vm.verifyUserData.apply(null, arguments)}}},[_vm._v("Crer un compte")])],1)],1)]],2)],1)])])])}
