<template>
    <section class="section section-shaped section-lg my-0">
        <div class="shape shape-style-1 bg-gradient-default">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
<bar-perso></bar-perso>
        
        <div class="container pt-lg-md">
          <div class="row"> <p class="text-white">Bienvenue {{utilisateur}}, super de te voir ici </p> </div>
            <div class="row justify-content-center">
                <div class="col-lg-5">
                    <card type="secondary" shadow
                          header-classes="bg-white pb-5"
                          body-classes="px-lg-5 py-lg-5"
                          class="border-0 m-3">

                        <template>
                          
        <div class="col-md-12">
            <base-button block type="primary" class=" mb-3" @click="modals.modal1 = true">
                Ajouter <br/>une ToDoList
            </base-button>
            <modal :show.sync="modals.modal1">
                <h6 slot="header" class="modal-title" id="modal-title-default">Cration de la To Do List</h6>

 <div id="titleForm" v-if="goToDiv==0">
                            <form role="form">
                                <base-input alternative
                                            class="mb-3"
                                            placeholder="titre de ma liste"
                                           v-model="modalData.title"
                                            required>
                                </base-input>

                                <div class="text-center">
                                    
                                    <base-button type="primary" class="my-4" @click="goToDiv++">Etape suivante</base-button>
                                </div>
                            </form>

 </div>
  <div class="col-10" id="sectorForm" v-if="goToDiv==1">
   
  <select class="form-select container" v-model="modalData.sector">
  <option disabled value="">Choisissez</option>
  <option v-for="sector in sectors" :key="sector">{{sector}}</option>

</select>
<span>Slectionn : {{ modalData.sector }}</span>

                                <div class="text-center">
                                    <base-button type="secondary" class="my-4" @click="goToDiv--">Etape prcdente</base-button>
                                    <base-button type="primary" class="my-4" @click="goToDiv++">Etape suivante</base-button>
                                </div>
                            
  </div>
   <div id="descriptionForm" v-if="goToDiv==2">
                                   <form role="form">
                                <base-input alternative
                                            class="mb-3"
                                            placeholder="dcris l'objectif de cette liste"
                                            v-model="modalData.description"
                                            required>
                                </base-input>

                                <div class="text-center">
                                                                        <base-button type="secondary" class="my-4" @click="goToDiv--">Etape prcdente</base-button>
                                    <base-button type="primary" class="my-4" @click="goToDiv++">Etape suivante</base-button>
                                </div>
                            </form>
   </div>
     <div id="taskForm" v-if="goToDiv==3">
                                   <form role="form">
                                                         <base-alert type="info" icon="ni ni-bell-55" dismissible>
                <span slot="text"><strong>Info!</strong>N'oubliez surtout pas de cliquer sur valider la tche et ajouter autant de tches que vous souhaitez! Vous pouvez changer le mode de tche entre  faire ou  viter</span>
            </base-alert>
<div class="row">
                    <div class="col-lg-12 col-sm-12">
                    <button class="btn btn-outline-light m-2" @click.prevent="switchSuccess=!(switchSuccess)">
                    <p>Cliquez ici pour changer de mode.</p>
                    <base-switch v-model="switchSuccess"  ></base-switch>
                    </button>
                    
                    <span class="clearfix" v-show="switchSuccess">Ecris nous une tche  effectuer </span>
                    <span class="clearfix" v-show="!(switchSuccess)">Ecris nous une tche  viter </span>
                    



                </div>

                
</div>
                <div class="col-lg-12 col-sm-12"  v-if="switchSuccess==true">
                                        <base-input v-model="textTask" placeholder="Ecris ce qu'il faut faire ici" :valid="true">
                    </base-input>
                </div>
                <div class="col-lg-12 col-sm-12"  v-if="switchSuccess==false">
                    <base-input v-model="textTask"  placeholder="Ecris ce qu'il faut viter ici" :valid="false" >
                    </base-input>
                </div>
                <div class="col-lg-12 col-sm-12">
<button class="btn btn-primary btn-sm" @click.prevent="addTask"> Valider la tche!</button>
                </div>
                <div class="row" v-for="(i,index) in tabTask" :key="index">
                <!--<base-alert type="success" icon="ni ni-like-2" dismissible>
                <span slot="text"><strong>Success!</strong> Bien ajout</span>
            </base-alert>-->
            <div  class="col-10 bg-success m-2" v-if="i.isToDo==true">
            
<div class="row">
    <div class="col-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-2">
        <button class="btn btn-danger btn-sm m-2" @click.prevent="deleteTask(index)">Supprimer</button>
    </div>
</div>
            </div>

                        <div  class="col-10 bg-danger m-2" v-if="i.isToDo==false">
            
<div class="row">
    <div class="col-8">
                        <p class="text-white">{{i.description}}</p>
                
    </div>
    <div class="col-2">
        <button class="btn btn-light btn-sm m-2" @click.prevent="deleteTask(index)">Supprimer</button>
    </div>
</div>
            </div>
                </div>

                                <div class="text-center">
                                    <base-button type="secondary" class="my-4" @click="goToDiv--">Etape prcdente</base-button>
                                    <base-button type="primary" class="my-4" @click="goToDiv++">Etape suivante</base-button>
                                </div>
                            </form>
   </div>
     <div class="col-10" id="succeedForm" v-if="goToDiv==4">
                        <p>A combien estimer vous votre russite pour ce projet? </p> 
                        <p class="text-success h2" v-if="sliders.slider1>=50">{{sliders.slider1}} % </p> 
                        <p class="text-warning h2" v-else-if="sliders.slider1>40">{{sliders.slider1}} % </p>  
                        <p class="text-danger h2" v-else>{{sliders.slider1}} % </p>       
                    <base-slider v-model="sliders.slider1">

                    </base-slider>
                                                        <base-button type="secondary" class="my-4" @click="goToDiv--">Etape prcdente</base-button>
                                    


   </div>

                <template slot="footer">
                    <base-button type="success" class="my-4" v-if="goToDiv==4" @click="sendToDoListToBackend">Valider sa To Do List</base-button>
                    <base-button type="link" class="ml-auto" @click="modals.modal1 = false">Fermer
                    </base-button>
                </template>
            </modal>
        </div>

                        </template>
                    </card>
                    
                </div>
                
                
            </div>
            <div class="row">
                        <div class="col-lg-5" v-for="(uneCard,index) in tableauCards" :key="index">
<card-perso v-bind:uneCard="uneCard"></card-perso>
                
                
            </div>
            </div>

        </div>
    </section>

</template>
<script>
import Modal from "@/components/Modal.vue";
import BaseSlider from "../components/BaseSlider.vue";
import BaseDropdown from "../components/BaseDropdown.vue";
import BaseInput from "../components/BaseInput.vue";
import Tabs from "@/components/Tabs/Tabs.vue";
import TabPane from "@/components/Tabs/TabPane.vue";
import BaseNav from "@/components/BaseNav";
import CardPerso from "./components/JavascriptComponents/CardPerso.vue";
import BarPerso from "./components/JavascriptComponents/BarPerso.vue";
import BaseAlert from "../components/BaseAlert.vue";
export default {
     components: {
    Modal,BaseSlider,BaseDropdown,BaseInput,Tabs,TabPane,BaseNav,CardPerso,BarPerso,BaseAlert
  },
 
  data() {
    return {
      modals: {
        modal1: false,
        modal2: false,
        modal3: false, 
      },
      modalData:{title:'',
      sector:'',
      description:'',
      rateSucceed:'',
     listOfTasks:[],
      
      },
       utilisateur:JSON.parse(localStorage.leTokenUserAchieve).pseudo,
      tabTask:[],
      switchSuccess:true,
            sliders: {
        slider1: 50,
        slider2: [150, 400]
      },
      tableauCards:[],
      textTask:'',
      goToDiv:0,
      sectors:["Activits sociales",
"Agriculture et Pche",
"Arts et Faonnage d'ouvrages d'art",
"Arts et spectacles",
"Assurance",
"Banque",
"Btiment et Travaux publics",
"Bien-tre",
"Collection et passe-temps",
"Commerce",
"Communication",
"Construction",
"Cuisine",
"Danse",
"Emploi",
"Entrepreunariat",
"Espaces naturels et Espaces verts",
"Htellerie-Restauration",
"Industrie",
"Informatique",
"Installation et Maintenance",
"Loisirs cratifs",
"Loisirs d'enrichissement",
"Loisirs de plein air",
"Loisirs domestiques",
"Mdecine traditionnelle",
"Mdia et Multimdia",
"Sant",
"Services  la collectivit",
"Services  la personne",
"Spectacle",
"Sports",
"Support  l'entreprise",
"Tourisme",
"Transport et Logistique",
"Vente et Grande distribution",
"Voyage"]
    };
  },
  methods: {
      addTask(){
          let dict = {description:'', isToDo:true};
          if(this.textTask.trim())
          {
              dict.description = this.textTask;
              dict.isToDo = this.switchSuccess;
              this.tabTask.push(dict);
              this.textTask="";
             
          }
      },
      deleteTask(ith){
          let tabInt=[];
          let n = this.tabTask.length;
          for(let i=0;i<n;i++)
          {
              if(i!=ith)
              {
                  tabInt.push(this.tabTask[i])
              }
          }

          this.tabTask = tabInt;
      },
      
      sendToDoListToBackend(){
          this.modalData.rateSucceed=this.sliders.slider1;
          this.modalData.listOfTasks=this.tabTask;
               
               fetch("https://achieve-back.herokuapp.com/card/addCard", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            userId: JSON.parse(localStorage.leTokenUserAchieve).userId,
                    title: this.modalData.title,
        sector: this.modalData.sector,
        description: this.modalData.description,
        rateSucceed:this.modalData.rateSucceed,
        pseudo:JSON.parse(localStorage.leTokenUserAchieve).pseudo,
        socialNetwork:JSON.parse(localStorage.leTokenUserAchieve).socialNetwork,

          }),
        })
        .then((result) => {
        
        return result.json();
      })
        .then((result) => {
          
//debut
               return fetch("https://achieve-back.herokuapp.com/card/"+result.id+"/tasks", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization:
              "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
          },
          body: JSON.stringify({
            userId: JSON.parse(localStorage.leTokenUserAchieve).userId,
        listOfTasks: this.modalData.listOfTasks,

          }),
        })
//fin

        }).then(() => {
                    console.log(this.tabTask);
          console.log(this.modalData.listOfTasks);
          //location.reload();
        })
        .catch(()=>console.log('erreur'));
      },
      
      
  },
    created() {
    
    fetch("https://achieve-back.herokuapp.com/card/", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.leTokenUserAchieve).token,
      },
    })
      .then((result) => {
       
        return result.json();
      })
      .then((tableauCards) => {
        
        this.tableauCards = tableauCards;
      });
  }, 
};
</script>
<style>
</style>
